import { useLoadScript } from '@react-google-maps/api'
import CustomGMap from './CustomGMap';
const GMapAPIKey = process.env.REACT_APP_GOOGLE_MAPS_API_KEY;
const libraries = ['places']; // declared and initialized here to prevent performance issues.

function GoogleMapsPlaces({ onAddressChange, onFieldTouch, onFormSubmission }) {
    // API Provider? Wrapper to provide child components certain accessibility rights
    const { isLoaded } = useLoadScript({
        googleMapsApiKey: `${GMapAPIKey}`,
        libraries: libraries, // help w/ map loading.
    })
    console.log()
    if (!isLoaded) {
        return <div>Loading...</div>;
    } else {
        return (<CustomGMap onSetAddr={onAddressChange} onInputTouch={onFieldTouch} onFormSubmission={onFormSubmission} />)
    }
}

export default GoogleMapsPlaces;
