import { useEffect } from 'react';
import classes from './PlacesAutoComplete.module.css'

import usePlacesAutocomplete, {
    getGeocode,
    getLatLng,
} from "use-places-autocomplete"; // helper functions

import {
    Combobox, // disp. user i/p
    ComboboxInput,
    ComboboxPopover,
    ComboboxList,
    ComboboxOption,
} from "@reach/combobox";
import "@reach/combobox/styles.css";

const PlacesAutoComplete = ({ setSelected , onUpdateAddressAutoComp, inputTouch, inputFieldReset}) => {
    const {
        ready,
        value,
        setValue,
        suggestions: { status, data }, // fetched correctly?, and all of data.
        clearSuggestions,
    } = usePlacesAutocomplete(); // hook call for User Interface

    const handleSelect = async (address) => { // convert address selection to lat long
        onUpdateAddressAutoComp(address)       
        setValue(address, false)  // OW the typed data
        
        clearSuggestions();
        const results = await getGeocode({ address }); // get geogode of address result
        const { lat, lng } = await getLatLng(results[0]); // cvt result to lat lng
        setSelected({ lat, lng }); // set selected value and render marker on pos.
    };

    useEffect(() => {
        if(inputFieldReset) {
            setValue('');
        }
    }, [inputFieldReset, setValue])

    const handleChange = (event) => {
        setValue(event.target.value)

    }
    return <>
        <Combobox onSelect={handleSelect} nullabe={true}>
            {/* event, we can call setVal func and set the changes typed * disabled if the autocomplete data is ready*/}
            <ComboboxInput onBlur={inputTouch} value={value} onChange={handleChange} disabled={!ready}
                className={classes["combobox-input"]} placeholder="Search an address" />
            {/* Result suggestion */}
            <ComboboxPopover>
                <ComboboxList>
                    {status === "OK" && data.map(({ place_id, description }) =>
                        <ComboboxOption key={place_id} value={description} />)}
                </ComboboxList>
            </ComboboxPopover>
        </Combobox>
    </>
}

export default PlacesAutoComplete