// Common hook for complex Sign up form validation and data entry.
import {
    useState
} from 'react';

const useInput = (validateEnteredValue) => {
    //** This hook is used to validate all kinds of input such as username/password/email*/
    const [enteredValue, setEnteredValue] = useState('');
    const [isFieldTouched, setIsFieldTouched] = useState(false);

    //** Executing callback function here to validate the input '' or 'a' or contains '@' */
    const valueIsValid = validateEnteredValue(enteredValue);
    // ** Combined case of input validation with field touch and entered value. */
    const valueHasError = !valueIsValid && isFieldTouched;

    const valueInputChangeHandler = event => {
        //* Keyboard stroke handler */
        setEnteredValue(event.target.value);
    }

    const addrInputChangeHandler = incString => {
        setEnteredValue(incString)
    }

    const valueInputBlurOrFocusHandler = () => {
        //* If user has touched input field, flag it */
        setIsFieldTouched(true);
    }

    const resetStates = () => {
        //* After the user has submitted the form, reset whatever input field states used*/
        setIsFieldTouched(false);
        setEnteredValue('');
    }

    return {
        //** Export all states & functions with K:V pairs */
        //** Value on the RHS is only for reference here, not used in the import */
        value: enteredValue,
        isInputValid: valueIsValid,
        valueHasError,
        valueInputChangeHandler,
        addrInputChangeHandler,
        valueInputBlurOrFocusHandler,
        resetStates,
    }

}

export default useInput;