import React, { useState, useEffect } from "react";
import { GoogleMap, Marker } from "@react-google-maps/api"
import classes from './CustomGMap.module.css'
import PlacesAutocomplete from './PlacesAutoComplete'


export default function CustomGMap({ onSetAddr, onInputTouch, onFormSubmission }) {
    // const center = useMemo(() => ({ lat: 43.45, lng: -80.49 }), []) // useMemo to load up cache of factory (which generates given data) from re-renders or on deps changes
    const [center, setCenter] = useState({ lat: 49.28, lng: -123.12 });
    const [zoom, setZoom] = useState(10);
    const [selectedCamPos, setSelectedCamPos] = useState(null); // For marker selection to keep state and show current state 
    useEffect(() => { // useEffect & useState working together to redirect camera to selected position
        if (!selectedCamPos) return;
        setCenter(selectedCamPos)
        setZoom(16);
    }, [selectedCamPos, zoom])
    return (<>
        <div className={classes["places-container"]}>
            {/* Pass location to render location as marker */}
            <PlacesAutocomplete setSelected={setSelectedCamPos} onUpdateAddressAutoComp={onSetAddr} inputTouch={onInputTouch} inputFieldReset={onFormSubmission} />
        </div>
        {/* Follow style of the parent container which is right-container in Tracker */}
        <div >
            <GoogleMap zoom={zoom} center={center} mapContainerStyle={{ width: '100%', height: '400px', marginTop: '10px', borderRadius: '0.5rem' }}>
                {/* If there is a selected marker and that something on the map was selected, position it and show it, but do show default container 
                mapID is needed from google Maps API to get custom styles and so do we need it for AdvancedMarkers too*/}
                {/* {selected && <Marker position={selected} />} */}
                {selectedCamPos && <Marker position={selectedCamPos} />}
            </GoogleMap>
        </div>
    </>)
}

